  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
}

*{
    font-family: Arial, Helvetica, sans-serif;
}
#ans::selection{
    background-color: rgba(0, 0, 0, 0.15);
}
#ans{
    background-color: rgba(0, 0, 0, .15);
}
.list-group-item-secondary{
    background-color: rgba(255, 255, 255, 0.8);
    border-color: rgba(0, 0, 0, 0.5);
    border-radius: 3px !important;
}
#code-input{
    display: block;
}
.alert-bottom{
    position: fixed;
    bottom: 5px !important;
    right: 25px !important;
    margin-left: 25px !important;
}
.border {
    border-width:2px !important;
}
.texts{
    padding-left: 10px;
    outline: none;
    border: none;
    background: transparent !important;
}

.info-hr{
    border-style: dashed;
}

.test-creation{
    border-color: #abced4 !important;
    background-color: rgba(0, 100, 100, 0.13) !important;
}
.test-form{
    box-shadow: aquamarine;
    background-color: rgba(0, 100, 100, 0.13) !important;
    border-color: #abced4 !important;
    border-width: 2px !important; 
}

.test-form-rounded{
    box-shadow: aquamarine;
    background-color: rgba(0, 100, 100, 0.13) !important;
    border-color: #abced4 !important;
    border-radius: 5px;
    border-width: 2px !important; 
}

.test-form .bg-success{
    background-color:  rgba(0, 139, 139, 0.516) !important; 
}

.test-form .bg-danger{
    background-color:  rgba(255, 139, 139, 0.8) !important; 
}


hr{
    border: 1px solid #1313136b;
    margin-bottom: 20px;
}


.invitation-code{
    font-size: 1.6em;
    font-weight: 600;
}

.code-share{
    height: 35px !important;
    position: relative;
    display: flex;
    flex-wrap: wrap;
}

.code-share > button{
    height: 40px;
    margin-left: 15px;
}
.code-share .card{
    font-size: 1.5rem;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: 10px;
    color: green;
}

.bg-light{
    background-color: rgba(255, 255, 255, 0.6) !important;
    border-radius: 3px !important;
}


*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.main-title {
    color: #2d2d2d;
    text-align: center;
    text-transform: capitalize;
    padding: 0.7em 0;
}

.container {
    padding: 1em 0;
}


.container .title {
    color: #1a1a1a;
    text-align: center;
    margin-bottom: 10px;
}

.content {
    position: relative;
    width: 100%;
    margin: auto;
    overflow: hidden;
}

.content .content-overlay {
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    transition: all 0.4s ease-in-out 0s;
}

.content:hover .content-overlay {
    opacity: 1;
}

.content-details {
    position: absolute;
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;
    width: 100%;
    top: 50%;
    left: 50%;
    opacity: 0;
    transform: translate(-50%, -50%);
    transition: all 0.4s ease-in-out 0s;
}

.content:hover .content-details {
    top: 50%;
    left: 50%;
    opacity: 1;
}

.content-details h3 {
    color: #fff;
    font-weight: 500;
    letter-spacing: 0.15em;
    margin-bottom: 0.5em;
    text-transform: uppercase;
}

.content-details p {
    color: #fff;
    font-size: 1rem;
}


.fadeIn-top {
    top: 20%;
}

.fadeIn-left {
    left: 20%;
}


.fadeIn-right {
    left: 80%;
}
